import Window from '../window/Window'
import './BusinessCard.scss'
import LinkedInIcon from '@mui/icons-material/LinkedIn';

const BusinessCard = ({ name = "Unknown", jobDescription = "Jobless", testimonial = "Great Person", linkedInUrl = "" }) => {
    const openLinkedInPage = () => {
        if (linkedInUrl === "") return;
        window.open(linkedInUrl, "noreferrer noopener")
    }


    return (
        <Window title={`// ${name}`}>
            <div className='businessCard'>
                <div className='titleAndButton'>
                    <h1>{jobDescription}</h1>
                    {linkedInUrl !== ""
                        ? <div onClick={openLinkedInPage} className='linkedInButton'>
                            <LinkedInIcon sx={{
                                fontSize: "3em"
                            }} />
                        </div>
                        : null}
                </div>
                <p>"{testimonial}"</p>
            </div>
        </Window>
    )
}

export default BusinessCard;
