import BlinkingCaret from "../../blinking_caret/BlinkingCaret";
import Window from "../../window/Window";
import './PreviousWork.scss';
import Container from "../../container/Container";
import HighlightText from "../../highlight/HighlightText";
import Highlight from "../../highlight/Highlight";

const PreviousWork = () => {
    return (
        <Container title="Previous Work">
            <div className="previousWorkContainer">
                <Window title="russian_roulette.py" closed={true}>
                    <p><HighlightText color="magenta" text="import" /> <HighlightText color="white" text="random" /> </p>
                    <p><HighlightText color="magenta" text="import" /> <HighlightText color="white" text="os" /> </p>
                    <p style={{ marginBottom: "20px" }}></p>
                    <p><HighlightText color="magenta" text="if" /> <HighlightText color="white" text="random" /><HighlightText color="gray" text="." /><HighlightText color="aqua" text="randint" /><HighlightText color="orange" text="(0" /><HighlightText color="gray" text="," /> <HighlightText color="orange" text="6)" /><HighlightText color="lightblue" text="==" /> <HighlightText color="orange" text="1" /><HighlightText color="white" text=":" /></p>
                    <p style={{ marginLeft: "20px" }}><HighlightText color="white" text="os" /><HighlightText color="gray" text="." /><HighlightText color="aqua" text="remove" /><HighlightText color="orange" text="(" /><HighlightText color="lightgreen" text="'C:\Windows\System32'" /><HighlightText color="orange" text=")" /></p>
                </Window>
                <Window title="selfie_23928.png" maxWidth="600px" closed={true} contentPadding="0">
                <img src="https://github.com/SaleemTheKing/image_hosting/blob/master/portfolio/my_dad.jpg?raw=true" alt='selfie_23928.png' />
            </Window>
                <Window title="previous_work.txt">
                    <div className="note">
                        <p>Take a look at my previous projects</p>
                        <p>I have separated them in <HighlightText color="yellow" text="4" /> categories:</p>
                        <p>
                            <Highlight color="aqua">
                                <ul style={{ marginLeft: "20px" }}>
                                    <li><HighlightText color="aqua" text="Frontend" /></li>
                                    <li><HighlightText color="aqua" text="Backend" /></li>
                                    <li><HighlightText color="aqua" text="DevOps" /></li>
                                    <li><Highlight color="aqua">Hobby<BlinkingCaret /></Highlight></li>
                                </ul>
                            </Highlight>
                        </p>
                    </div>
                </Window>
            </div>
        </Container>
    )
}

export default PreviousWork;