import './Contact.scss';
import { useState } from 'react';
import Window from '../../window/Window';
import Container from '../../container/Container';
import BlinkingCaret from '../../blinking_caret/BlinkingCaret';

const Contact = ({ maxMessageLength = 2000 }: any) => {
    const [counter, setCounter] = useState(0);

    const updateCounter = (e: any) => {
        setCounter(e.target.value.length)
    }

    return (
        <Container title="Contact">
            <Window title="lets_chat.exe" randomSpawn={false} maxWidth="500px">
                <div className="contactContainer">
                    <h1>Let's chat!<BlinkingCaret /></h1>
                    <form action='mailto:saleem031998@gmail.com' method="post" encType='text/plain'>
                        <div>
                            <label htmlFor="name" style={{ fontWeight: "bold" }}>First name</label>
                            <input name="name" type='text' />
                        </div>
                        <div>
                            <label htmlFor="email" style={{ fontWeight: "bold" }}>Email</label>
                            <input name="email" type='email' />
                        </div>
                        <div>
                            <label htmlFor="message" style={{ fontWeight: "bold" }}>Message</label>
                            <textarea
                                name="message"
                                maxLength={maxMessageLength}
                                onChange={updateCounter}
                            />
                            <div className='textAreaCounter'>{counter}/{maxMessageLength}</div>
                        </div>
                        <div className='sendButtonContainer'>
                            <button type='submit'>Send</button>
                        </div>
                    </form>
                </div>
            </Window >
        </Container>
    )
}

export default Contact;