import './Window.scss';
import Draggable from 'react-draggable';
import MinimizeIcon from '@mui/icons-material/Minimize';
import FitScreenIcon from '@mui/icons-material/FitScreen';
import CloseIcon from '@mui/icons-material/Close';
import InsertDriveFileIcon from '@mui/icons-material/InsertDriveFile';
import { useState, useId, useEffect } from 'react';
import { motion, useAnimate } from 'framer-motion';

class WindowIndex {
    static index = 10;
    static updateIndex() { WindowIndex.index++; }
}

const Window = ({ title = "title.exe", closed = false, minimized = false, maxWidth = "none", maxHeight = "none", contentPadding = "20px", randomSpawn = true, children }: any) => {
    const windowId = useId();
    const [closedState, setClosed] = useState<boolean>(closed);
    const [minimizedState, setMinimized] = useState<boolean>(minimized);
    const [currentZIndex, setZIndex] = useState<number>(closed ? 0 : WindowIndex.index);
    const [reveal, revealAnim] = useAnimate();
    const fileName = title.replaceAll("//", "").trim().replaceAll(" ", "_").toLowerCase();

    const isMobileDevice = window.innerWidth <= 768;
    const initialX = randomSpawn && !isMobileDevice ? Math.floor(Math.random() * (window.innerWidth / 1.75)) : 0;
    const initialY = randomSpawn && !isMobileDevice ? (Math.random() * (window.innerHeight) / 4) : 0;

    useEffect(() => {
        if (!closedState) {
            revealAnim(reveal.current, { opacity: 1 }, { duration: 1 })
            prioritise();
        } else {
            revealAnim(reveal.current, { opacity: 0 }, { duration: 1 })
        }
    })

    const minimize = () => {
        setMinimized(true);
    }

    const maximize = () => {
        setMinimized(false);
    }

    const closeFile = () => {
        setClosed(true);
        setMinimized(false);
        setZIndex(0);
    }

    const openFile = () => {
        setClosed(false);
    }

    const prioritise = () => {
        if (closedState) {
            setZIndex(5);
        }
        else {
            setZIndex(WindowIndex.index);
        }
    }

    const deprioritise = () => {
        if (closedState) return;
        WindowIndex.updateIndex();
    }

    return (
        <Draggable handle='.windowTitle' bounds={"section"} defaultPosition={{ x: initialX, y: initialY }} onStart={prioritise} onStop={deprioritise}>
            <div className="windowWrapper"
                style={{
                    maxWidth: maxWidth,
                    maxHeight: maxHeight,
                    zIndex: currentZIndex,
                    position: "absolute"
                }}
            >
                <div className='windowContainer' id={windowId} hidden={closedState}>
                    <div className="windowTitle" onDoubleClick={maximize}>
                        <div>
                            <h1>{title}</h1>
                        </div>
                        <div className='windowButtons'>
                            <div className="windowButton" onClick={minimize} onTouchStart={minimize} hidden={minimizedState}><MinimizeIcon /></div>
                            <div className="windowButton" onClick={maximize} onTouchStart={maximize} hidden={!minimizedState}><FitScreenIcon /></div>
                            <div className="windowButton" onClick={closeFile} onTouchStart={closeFile}><CloseIcon /></div>
                        </div>
                    </div>
                    <div style={{ padding: contentPadding }} hidden={minimizedState}>
                        <motion.div ref={reveal} style={{ maxHeight: maxHeight }}>
                            {children}
                        </motion.div>
                    </div>
                </div>
                <div hidden={!closedState}>
                    <div className='file'>
                        <div className='windowTitle'>
                            <div onDoubleClick={openFile} onTouchStart={openFile} className='fileContainer'>
                                <InsertDriveFileIcon sx={{
                                    fontSize: "inherit"
                                }} />
                            </div>
                        </div>
                        <div><h2 className='fileName'>{fileName}</h2></div>
                    </div>
                </div>
            </div>
        </Draggable>
    )
}

export default Window;