import { motion } from 'framer-motion';

const BlinkingCaret = ({ caret = "▌" }: any) => {
    const initial = { opacity: 0 };
    const animate = { opacity: 1 };

    return (
        <motion.span style={{textShadow: "inherit"}}
            initial={initial}
            animate={animate}
            transition={{
                repeat: Infinity,
                duration: .5,
                repeatType: "mirror"
            }}>
            {caret}
        </motion.span>
    )
}

export default BlinkingCaret;