import { useEffect, useRef } from 'react';
import './Pipeline.scss';
import { motion, useScroll, useTransform } from 'framer-motion';
import CheckIcon from '../images/check_circle_outline.svg';
import PauseIcon from '../images/pause_circle_outline.svg';
import RunningIcon from '../images/radio_button_checked.svg';

const Pipeline = ({ children }) => {
    const ref = useRef();

    const { scrollYProgress } = useScroll({
        target: ref,
        offset: ["start start", "end start"]
    });

    const readProgress = useTransform(scrollYProgress, [0, 1], [0, children.length]);

    useEffect(() => {
        readProgress.on('change', (latestValue) => {
            const val = Math.round(latestValue);
            for (let i = 1; i <= children.length - 1; ++i) {
                const pipelineCircleRunning = document.getElementById(`pipelineCircleRunning-${i}`);
                const pipelineCirclePause = document.getElementById(`pipelineCirclePause-${i}`);
                const pipelineCircleComplete = document.getElementById(`pipelineCircleComplete-${i}`);
                const pipelineJobDetails = document.getElementById(`pipelineJobDetails-${i}`);
                const pipelineDiv = document.getElementsByClassName("pipeline")[0];

                pipelineCircleRunning.hidden = true;

                if (i < val) {
                    pipelineCircleComplete.hidden = false;
                    pipelineCirclePause.hidden = true;
                    pipelineCircleRunning.hidden = true;
                    pipelineJobDetails.children[0].children[1].innerHTML = "Success"
                }
                
                // Currently Reading
                if (i === val) {
                    pipelineCirclePause.hidden = true;
                    pipelineCircleRunning.hidden = false;
                    pipelineCircleComplete.hidden = true;
                    pipelineJobDetails.children[0].children[1].innerHTML = "Reading"
                    


                    pipelineDiv.scrollTo({
                        left: (pipelineDiv.scrollWidth / children.length) * (i - 1),
                        behavior: 'smooth'
                    });
                }

                
                // To read
                if (i > val) {
                    pipelineCirclePause.hidden = false;
                    pipelineCircleRunning.hidden = true;
                    pipelineCircleComplete.hidden = true;
                    pipelineJobDetails.children[0].children[1].innerHTML = "Waiting for job"
                }
            }
        })
    })

    return (
        <motion.div ref={ref}>
            <motion.div className="pipelineContainer">
                <motion.div className='pipeline'>
                    {children.map((element, i) => (
                        element !== children[0] ?
                            <div key={i} className='pipelineJobContainer' id={`pipelineJobContainer-${i}`}>
                                <div className='pipelineJob' onClick={() => window.location.href = `#${element.props.id}`}>
                                    <motion.div id={`pipelineCircleRunning-${i}`} key={`pipelineCircleRunning-${i}`} style={{ transition: ".5s" }} hidden>
                                        <motion.img
                                            className='pipelineIcon runningIcon'
                                            src={RunningIcon}
                                        />
                                    </motion.div>
                                    <motion.div id={`pipelineCircleComplete-${i}`} key={`pipelineCircleComplete-${i}`} style={{ transition: ".5s" }} hidden>
                                        <motion.img
                                            className='pipelineIcon checkIcon'
                                            src={CheckIcon}
                                        />
                                    </motion.div>
                                    <motion.div id={`pipelineCirclePause-${i}`} key={`pipelineCirclePause-${i}`} style={{ transition: ".5s" }}>
                                        <motion.img
                                            className='pipelineIcon pauseIcon'
                                            src={PauseIcon}
                                        />
                                    </motion.div>
                                    <div className='pipelineJobDetails' id={`pipelineJobDetails-${i}`}>
                                        <div>
                                            <h1>{element.props.id}</h1>
                                            <p>Waiting for job</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            : null
                    ))}
                </motion.div>
            </motion.div>
            <motion.div>
                {children}
            </motion.div>
        </motion.div>
    );
}

export default Pipeline;
