import Container from "../../container/Container";
import Window from "../../window/Window";
import BlinkingCaret from "../../blinking_caret/BlinkingCaret";
import Highlight from "../../highlight/Highlight";
import HighlightText from "../../highlight/HighlightText";

const Frontend = () => {
    return (
        <Container title="Frontend projects">
            <Window title="cute_cat.jpg" contentPadding="0" closed={true}>
                <img src="https://github.com/SaleemTheKing/image_hosting/blob/master/portfolio/cute_cat.jpg?raw=true" alt="cute_cat.jpg" />
            </Window>
            <Window title="frontend_skills.txt" maxWidth="325px" closed={true}>
                <div className="note">
                    <Highlight color="yellow">
                        <ul>
                            <li><HighlightText color="yellow" text={"Typescript"} /></li>
                            <li><HighlightText color="yellow" text={"JavaScript"} /></li>
                            <li><HighlightText color="yellow" text={"React"} /></li>
                            <li><HighlightText color="yellow" text={"Vue"} /></li>
                            <li><HighlightText color="yellow" text={"d3.js"} /></li>
                            <li><HighlightText color="yellow" text={"elastic.s"} /></li>
                            <li><HighlightText color="yellow" text={"jQuery"} /></li>
                            <li><HighlightText color="yellow" text={"Flask"} /></li>
                        </ul>
                    </Highlight>
                </div>
            </Window>
            <Window title="data_visualisation.md" maxWidth="500px" closed={true}>
                <div className="note">
                    <p>This project required me to visualize the data from a new <HighlightText color="orange" text="API" /> provided by the client.</p>
                    <p>It was more of a <HighlightText color="orange" text="fullstack" /> project. Everything was done from scratch. From setting up the <HighlightText color="aqua" text="Postgres" /> database and <HighlightText color="orange" text="authentication" /> all the way to deploying using <HighlightText color="aqua" text="CloudFoundry" />.</p>
                    <p>The backend was written in <HighlightText color="aqua" text="Java" /> with the <HighlightText color="aqua" text="Play Framework" /> and the frontend was <HighlightText color="aqua" text="React" /> using <Highlight color="aqua">d3.js<BlinkingCaret /></Highlight></p>
                    <img src="https://github.com/SaleemTheKing/image_hosting/blob/master/portfolio/project_data_visualisation.png?raw=true" alt="portfolio.png" />
                </div>
            </Window>
            <Window title="portfolio_website.md" maxWidth="500px">
                <div className="note">
                    <p>Believe it or not but this website is the only <b>actual</b> frontend work I have done...</p>
                    <p>The entire website is made in <HighlightText color="aqua" text="React Typescript" /> and then released on <HighlightText color="aqua" text="GitHub" /> Pages<BlinkingCaret /></p>
                    <img src="https://github.com/SaleemTheKing/image_hosting/blob/master/portfolio/portfolio_screenshot.png?raw=true" alt="data_visualisation_project.png" />
                </div>
            </Window>
            <Window title="stager.md" maxWidth="500px">
                <div className="note">
                    <p><HighlightText color="orange" text="Stager" /> is a ticketing website comparable to <HighlightText color="orange" text="ticketmaster" />.</p>
                    <p>I only worked in the <HighlightText color="orange" text="backend" /> of the software which was in <HighlightText color="aqua" text="Java" />.</p>
                    <p>I have also written the entire <a href="https://stager.co/en/support/tickets-lost/" target="_blank" rel="noopener noreferrer"><HighlightText color="magenta" text="Lost Tickets API." /></a> Go ahead and click the link to see for yourself.</p>
                    <img src="https://github.com/SaleemTheKing/image_hosting/blob/master/portfolio/stager.png?raw=true" alt="portfolio.png" />
                </div>
            </Window>
            <Window title="user_management_website.txt" maxWidth="325px">
                <div className="note">
                    <p>I have worked on a website which is basically an administrative tool for managing users.</p>
                    <p>The site itself was made entirely in <HighlightText color="aqua" text="Python" /> using <HighlightText color="aqua" text="Flask" /></p>
                    <p>What the web application did was generate <HighlightText color="aqua" text="Powershell" /> scripts from the user interface and run the on a remote server. You could compare it to <HighlightText color="aqua" text="Rundeck" /></p>
                    <p>For rapid prototyping of the frontend we used <Highlight color="aqua">Material Design<BlinkingCaret /></Highlight></p>
                </div>
            </Window>
        </Container>
    )
}

export default Frontend;
