import Container from "../../container/Container";
import Window from "../../window/Window";
import Highlight from "../../highlight/Highlight";
import HighlightText from "../../highlight/HighlightText";
import BlinkingCaret from "../../blinking_caret/BlinkingCaret";

const Backend = () => {
    return (
        <Container title="Backend projects">
            <Window title="printer_manual.pdf" closed={true} maxWidth="300px">
                <div className="note">
                    <p><HighlightText color="red" text="Don't let them know you lied about the 'fixing your printer' part" /></p>
                </div>
            </Window>

            <Window title="databases.txt" maxWidth="500px" closed={true}>
                <div className="note">
                    <p>Ahhhhhh <HighlightText color="orange" text="databases" />. I like them because they <HighlightText color="red" text="never" /> forget.</p>
                    <p>From database design to implementation in code, here is a list of databases I have worked with:
                        <Highlight color="aqua">
                            <ul>
                                <li><HighlightText color="aqua" text="PostgreSQL" /></li>
                                <li><HighlightText color="aqua" text="MongoDB" /></li>
                                <li><HighlightText color="aqua" text="ElasticSearch" /></li>
                                <li><HighlightText color="aqua" text="Apache Cassandra" /></li>
                                <li><HighlightText color="aqua" text="CouchDB" /></li>
                                <li><HighlightText color="aqua" text="InfluxDB" /></li>
                                <li><HighlightText color="aqua" text="TimescaleDB" /></li>
                                <li><HighlightText color="aqua" text="Redis" /></li>
                                <li><HighlightText color="aqua" text="MySQL" /></li>
                                <li><HighlightText color="aqua" text="Microsoft Azure SQL" /></li>
                                <li><HighlightText color="aqua" text="Oracle Database" /></li>
                                <li><HighlightText color="aqua" text="Greenplum" /></li>
                                <li><Highlight color="aqua">QuestDB<BlinkingCaret /></Highlight></li>
                            </ul>
                        </Highlight>
                    </p>
                </div>
            </Window>

            <Window title="api_development.cpp" maxWidth="500px">
                <div className="note">
                    <p>I have developed many <HighlightText color="orange" text="API's" />, ranging from <HighlightText color="orange" text="authentication" /> using <HighlightText color="aqua" text="oAuth" /> to <HighlightText color="aqua" text="SMS" /> to <HighlightText color="aqua" text="database" /> related projects.</p>
                    <p>These <HighlightText color="orange" text="API's" /> have been developed in several different languages like:
                        <ul>
                            <li><HighlightText color="aqua" text="Python" /> using <HighlightText color="aqua" text="FastAPI" /></li>
                            <li><HighlightText color="aqua" text="C#" /> using <HighlightText color="aqua" text="ASP.NET" /></li>
                            <li><HighlightText color="aqua" text="Java" /> using <Highlight color="aqua">Play Framework<BlinkingCaret /></Highlight></li>
                        </ul>
                    </p>
                    <img src="https://github.com/SaleemTheKing/image_hosting/blob/master/portfolio/swagger.png?raw=true" alt="swagger" />
                </div>
            </Window>

            <Window title="backend_skills.txt" maxWidth="500px">
                <div className="note">
                    <p>Programming / scripting languages
                        <Highlight color="aqua">
                            <ul>
                                <li><HighlightText color="aqua" text="C#" /></li>
                                <li><HighlightText color="aqua" text="Python" /></li>
                                <li><HighlightText color="aqua" text="Java" /></li>
                                <li><HighlightText color="aqua" text="C++" /></li>
                                <li><HighlightText color="aqua" text="Perl, I know, please don't ask" /></li>
                                <li><HighlightText color="aqua" text="Typescript" /></li>
                                <li><HighlightText color="aqua" text="JavaScript" /></li>
                                <li><HighlightText color="aqua" text="Powershell scripting" /></li>
                                <li><HighlightText color="aqua" text="Bash scripting" /></li>
                            </ul>
                        </Highlight>
                    </p>
                    <p>General skills
                        <Highlight color="aqua">
                            <ul>
                                <li><HighlightText color="aqua" text="GitHub" /></li>
                                <li><HighlightText color="aqua" text="GitLab" /></li>
                                <li><HighlightText color="aqua" text="Scrum, agile and SAFe 6" /></li>
                                <li><HighlightText color="aqua" text="Subversion, again don't ask" /></li>
                                <li><HighlightText color="aqua" text="Fixing your printer" /></li>
                            </ul>
                        </Highlight>
                    </p>
                    <p>Databases
                        <ul>
                            <li>see <Highlight color="pink">databases.txt<BlinkingCaret /></Highlight></li>
                        </ul>
                    </p>
                </div>
            </Window>
        </Container>
    )
}

export default Backend;