import './AboutMe.scss';
import BlinkingCaret from '../../blinking_caret/BlinkingCaret';
import Container from '../../container/Container';
import Window from '../../window/Window';
import HighlightText from '../../highlight/HighlightText';
import Highlight from '../../highlight/Highlight';

const AboutMe = () => {
    return (
        <Container title="About me">
            <Window title="frontend.txt" maxWidth="600px" closed={true}>
                <div className='note'>
                    <p>This website is my only experience and that's it :(</p>
                    <p>Then again, only a mind as <HighlightText color="orange" text="creative" /> as mine can come up with a solution to put myself out there in way never seen before!</p>
                    <p>If you wonder what I used to create this website, it is <b>100%</b> <HighlightText color="aqua" text="React Typescript" />, lots of <HighlightText color="aqua" text="CSS" /> magic and a creative mind!<BlinkingCaret /></p>
                </div>
            </Window>
            <Window title="backend.txt" maxWidth="600px">
                <div className='note'>
                    <p>With my <HighlightText color="orange" text="5+ years" /> of workexperience in the backend of software. My career is driven by my passion for code.</p>
                    <p>Embracing the role of a true backend engineer, I will seamlessly blend my <HighlightText color="orange" text="critical thinking" /> and the endusers needs to deliver an innovative solution!<BlinkingCaret /></p>
                </div>
            </Window>
            <Window title="devops.txt" maxWidth="600px">
                <div className='note'>
                    <p>With my <HighlightText color="orange" text="2+ years" /> of workexperience in the automation of deploying and integrating software. I can put myself between the company and infrastructure the software.</p>
                    <p>From developing software all the way to building <HighlightText color="orange" text="pipelines" /> in <HighlightText color="aqua" text="GitLab/GitHub" />. I will see it through till the very end.</p>
                </div>
            </Window>
            <Window title="system_admin.txt" maxWidth="600px">
                <div className='note'>
                    <p>With my <HighlightText color="orange" text="2+ years" /> of workexperience in the automation of administrating servers and users. I will keep our projects safe!</p>
                    <p><HighlightText color="aqua" text="Active Directory" />, <HighlightText color="aqua" text="RedHat IPA" />, <HighlightText color="orange" text="user management" /> and <Highlight color="orange">server management<BlinkingCaret /></Highlight></p>
                </div>
            </Window>
        </Container>
    )
}

export default AboutMe;