import Container from "../../container/Container";
import Window from "../../window/Window";
import HighlightText from "../../highlight/HighlightText";
import Highlight from "../../highlight/Highlight";
import BlinkingCaret from "../../blinking_caret/BlinkingCaret";


const DevOps = () => {
    return (
        <Container title="DevOps projects">
            <Window title="provisioning.pdf" maxWidth="650px">
                <div className="note">
                    <p>I have managed over <HighlightText color="yellow" text="150" /> servers using <HighlightText color="aqua" text="Ansible" /> and <HighlightText color="aqua" text="GitLab pipelines" />.</p>
                    <p>I can help deploying your <HighlightText color="aqua" text="Docker" /> containers with <HighlightText color="aqua" text="Kubernetes" /> on <HighlightText color="aqua" text="AWS" /> or <HighlightText color="aqua" text="Azure" /></p>
                </div>
            </Window>
            <Window title="open_source.pipeline" maxWidth="550px">
                <div className="note">
                    <p>Another project of mine was helping a company publish multiple of their <HighlightText color="orange" text="open source" /> projects to <HighlightText color="aqua" text="NPM" /> with <Highlight color="aqua">GitHub Actions<BlinkingCaret/></Highlight></p>
                    <img src="https://github.com/SaleemTheKing/image_hosting/blob/master/portfolio/npm.png?raw=true" alt="npm_screenshot"/>
                </div>
            </Window>
            <Window title="my_car.jk" maxWidth="550px" closed={true} contentPadding="">
                <div className="note">
                    <img src="https://github.com/SaleemTheKing/image_hosting/blob/master/portfolio/no.jpg?raw=true" alt="lambo" />
                </div>
            </Window>
            <Window title="devops_skills.txt" maxWidth="550px" closed={true}>
            <div className="note">
                    <Highlight color="yellow">
                        <ul>
                            <li><HighlightText color="yellow" text="Docker"/></li>
                            <li><HighlightText color="yellow" text="Kubernetes"/></li>
                            <li><HighlightText color="yellow" text="GitLab Pipelines"/></li>
                            <li><HighlightText color="yellow" text="GitHub Actions"/></li>
                            <li><HighlightText color="yellow" text="Ansible"/></li>
                            <li><HighlightText color="yellow" text="Scripting (Linux and Windows)"/></li>
                            <li><HighlightText color="yellow" text="AWS"/></li>
                            <li><Highlight color="yellow">Azure<BlinkingCaret/></Highlight></li>
                        </ul>
                    </Highlight>
                </div>
            </Window>
        </Container>
    )
}

export default DevOps;