import BlinkingCaret from '../../blinking_caret/BlinkingCaret';
import Window from '../../window/Window';
import './Homepage.scss';
import Container from '../../container/Container';
import MinimizeIcon from '@mui/icons-material/Minimize';
import FitScreenIcon from '@mui/icons-material/FitScreen';
import CloseIcon from '@mui/icons-material/Close';
import InsertDriveFileIcon from '@mui/icons-material/InsertDriveFile';
import Highlight from '../../highlight/Highlight';
import HighlightText from '../../highlight/HighlightText';

const Homepage = () => {
    return (
        <Container title="PortfolioOS v4.20.69">
            <Window title="this_is_me.png" maxWidth="600px" contentPadding="0" closed={true}>
                <img src="https://github.com/SaleemTheKing/image_hosting/blob/master/portfolio/photo.png?raw=true" alt='saleem'/>
            </Window>
            <Window title="welcome.exe">
                <div className="textContainer">
                    <h2>saleem sarwar</h2>
                    <h1>Software Engineer<BlinkingCaret /></h1>
                    <div>
                        <button onClick={() => window.location.href = "#Backend"}>Latest work</button>
                        <button onClick={() => window.location.href = "#Contact"}>Contact me</button>
                    </div>
                </div>
            </Window>
            <Window title="README.md" maxWidth="600px">
                <div className='note'>
                    <p>These windows are interactive and can be opened and closed and moved around. Go ahead and try it!</p>
                    <p>The <Highlight color="orange"><MinimizeIcon /></Highlight> icon, minimizes the window but still shows the title.</p>
                    <p>The <Highlight color="orange"><FitScreenIcon /></Highlight> icon, opens a minimized window.</p>
                    <p>The <Highlight color="orange"><CloseIcon /></Highlight> icon, closes the "application"</p>
                    <p>The <Highlight color="orange"><InsertDriveFileIcon /></Highlight> icon, is a file that can be opened</p>
                    <p>I will highlight the important stuff <HighlightText color="aqua" text="like this!"/></p>
                </div>
            </Window>
        </Container>
    )
}

export default Homepage;