import { motion } from "framer-motion";
import './Intro.scss';
import { useEffect, useState } from "react";

const Intro = () => {
    const [hideOverlay, setHideOverlay] = useState(false);

    const textVariants = {
        initial: {
            opacity: 1,
        },
        animate: {
            opacity: 1,
            transition: {
                staggerChildren: 0.25,
                delayChildren: 1,
            }
        }
    }

    const letterVariants = {
        initial: {
            opacity: 0,
        },
        animate: {
            opacity: 1,
        }
    }

    const overlayVariants = {
        initial: {
            opacity: 1,
        },
        animate: {
            opacity: 0,
            transition: {
                delay: 8,
                duration: 2,
            },
        },
    }

    const loadingBarVariants = {
        initial: {
            width: 0
        },
        animate: {
            width: ["0%","30%","100%"],
            transition: {
                delay: 3,
                duration: 4,
                times: [0, 0.7, 1]
            }
        }
    }

    const handleAnimationComplete = () => {
        setHideOverlay(true);
    };

    useEffect(() => {
        if (hideOverlay) {
            document.body.style.overflow = "auto";
        }
    }, [hideOverlay])

    // If we refresh page while reading we dont want to see the animation again
    if (document.location.hash !== "") {
        document.body.style.overflow = "auto";
        return null
    };

    document.body.style.overflow = "hidden";

    return (
        <motion.div variants={overlayVariants} initial="initial" animate='animate' className={hideOverlay ? "overlay hide" : "overlay"} onAnimationComplete={handleAnimationComplete} >
            <motion.div variants={textVariants} initial="initial" animate='animate' className="lettersContainer">
                <motion.h1 variants={letterVariants}>P</motion.h1>
                <motion.h1 variants={letterVariants}>o</motion.h1>
                <motion.h1 variants={letterVariants}>r</motion.h1>
                <motion.h1 variants={letterVariants}>t</motion.h1>
                <motion.h1 variants={letterVariants}>f</motion.h1>
                <motion.h1 variants={letterVariants}>o</motion.h1>
                <motion.h1 variants={letterVariants}>l</motion.h1>
                <motion.h1 variants={letterVariants}>i</motion.h1>
                <motion.h1 variants={letterVariants}>o</motion.h1>
                <motion.h1 variants={letterVariants}>O</motion.h1>
                <motion.h1 variants={letterVariants}>S</motion.h1>
            </motion.div>
            <motion.div variants={letterVariants} transition={{delay: 3}} className="loadingBarOutline">
                <motion.div variants={loadingBarVariants} initial='initial' animate='animate' className="loadingBar"></motion.div>
            </motion.div>
        </motion.div>
    )
}

export default Intro;